

import HeaderBar from '../../components/header.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'

export default {
    components: {
        HeaderBar,
        XInput,
        Group,
        XButton,
        Cell,
        Checker,
        CheckerItem,
        Tabbar
    },
    methods: {
        change(value) {
                console.log('change:', value)
            },
            processButton001() {
                this.submit001 = 'processing'
                this.disable001 = true

                this.$vux.loading.show({
                    text: 'Loading'
                })
                setTimeout(() => {
                    this.$vux.loading.hide()
                    this.submit001 = '修改'
                    this.disable001 = false
                }, 1000)
            }
    },
    data() {
        return {
            Property: '透天',
            code: '',
            account: '',
            jointly: '',
            submit001: '修改',
            disable001: false
        }
    }
}




