

import Group from 'vux/src/components/group/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {
    mapState, mapActions
}
from 'vuex'
import HeaderBar from '../../components/header.vue'

export default {
    components: {
        HeaderBar,
        Cell,
        Group,
        Swipeout,
        SwipeoutItem,
        SwipeoutButton,
        Flexbox,
        FlexboxItem,
        Scroller,
        Actionsheet,
        Tabbar,
        XButton
    },
    computed: {
        ...mapState([
            'rooms'
        ])
    },
    mounted() {
        this.getRooms(),
            this.$nextTick(() => {
                this.$refs.scroller.reset()
            })
    },
    methods: {
        ...mapActions(['getRooms']),
            onButtonClick(type, id) {
                switch (type) {
                    case 'tenancy':
                        this.show = true;
                        break;
                    case 'modify':
                        this.$router.push({
                            'path': '/room/' + id
                        })
                        break;
                    case 'delete':
                        this.show1 = true;
                        break;
                    default:
                }
            },
            handleEvents(type) {
                console.log('event: ', type)
            },
            load(uuid) {
                setTimeout(() => {
                    this.$refs.scroller.donePulldown()
                }, 2000)
            },
            click(key) {
                console.log(key)
            },
            onDelete() {
                this.showSuccess = true
            },
            processButton001() {
                this.$router.push({
                    'path': '#'
                })
            }
    },
    data() {
        return {
            show: false,
            menus: {
                'title.noop': '你確定嗎?<br/><span style="color:#666;font-size:12px;">退租後無法再撤銷.</span>',
                tenancy: '<span style="color:red">退租</span>'
            },
            show1: false,
            menus1: {
                'title.noop': '你確定嗎?<br/><span style="color:#666;font-size:12px;">刪除後無法再撤銷.</span>',
                delete: '<span style="color:red">刪除</span>'
            },
            submit001: '新增'
        }
    }
}




